<template>
    <div class="screen-overlay creation-overlay">
        <div style="flex: 1;" v-if="this.showReward">
            <div class="reward-info-container">
                <h3>!</h3>
                <h2 style="font-size: 250%; color:#00A460;">+{{fetchResultTreasure.rewardCreated}}</h2>
                <p>     </p>
            </div>
            <div class="confirm-button-container" >
                <button class="continue-button" @click="$emit('success-creation-overlay')"></button>
            </div>
        </div>
        <div class="creation-overlay-padding" v-if="!this.showReward">
            <!--<input 
            class="cancel-button" 
            v-if="this.prevTreasure" 
            type="button" 
            @click="$emit('change-state',this.appStates.high)" 
            value="" />-->
            <h2 class="title-1">{{this.prevTreasure ? "   " : "   "}}</h2>
            <h5 class="title-2">{{this.prevTreasure ? " ." : "   ." }}</h5>
            <div class="creation-overlay-container" @contextmenu="prevent">
                <span v-if="(prevTreasure && imgFiles.length > 0) || !prevTreasure">
                    <div v-for="index in Math.min(images.length+1,maxBlocks)" v-bind:key="index">
                        <CreationBlock
                        ref="creationBlock"
                        :index="index"
                        :textBlocks="textBlocks"
                        :images="images"
                        :prevData="prevTreasure"
                        :disableButton="disableButton"
                        @contextmenu="prevent"
                        @delete-block="deleteBlock"
                        @text-input="textInput"
                        @file-select="fileSelect"/>
                    </div>
                </span>
                <span v-if="(prevTreasure && imgFiles.length === 0)">
                     ({{this.imgFiles.length}}/{{this.prevTreasure.imageUrls.length}})
                </span>
                <!-- TODO: https://stackoverflow.com/a/27002935 -->
            </div>
            <button class="continue-button" @click="saveTreasureData" :disabled="this.disableButton">{{this.prevTreasure ? "" : ""}}</button> 
        </div>
        
    </div>
</template>

<script>
import CreationBlock from '@/components/CreationBlock.vue'
import rescaleImgCanvas from '@/RescaleImgCanvas.js'

export default {
    name: 'createEditTreasureOverlay',
    props: ['position','user','prevTreasure','appState','appStates'],
    components: {
        CreationBlock
    },
    async mounted(){
        if(this.prevTreasure){
            this.imgFiles = [];
            // NOTE: Treasure images are pre-fetched when overlay is first opened, before DOM img element is displayed
            //https://stackoverflow.com/a/53584010
            for (let i = 0; i < this.prevTreasure.imageUrls.length; i++){ 
                let response = await fetch(this.$endpoints.imageByUrl(this.prevTreasure.imageUrls[i]), {credentials: "include"});
                let data = await response.blob();
                let metadata = {type: 'image/jpeg'};
                this.imgFiles.push( new File([data],"lazyImg.jpg",metadata) );
            }
            this.images = this.imgFiles;
            this.textBlocks = this.prevTreasure.textBlocks;
        }
    },
    data(){ 
        return{
            textBlocks: [],
            images: [],
            forceDisableButton: false,
            fetchResultTreasure: undefined,
            showReward: false,
            maxBlocks: 3,
            imgFiles: []
        }
    },
    methods: {
        prevent(e){ e.preventDefault(); },
        async saveTreasureData(){
            this.forceDisableButton = true;
            let uploadPromise = new Promise((resolve)=> this.uploadFiles(resolve) )
            for (let i = 0; i < this.textBlocks.length; i++) 
                if(this.textBlocks[i] === null || this.textBlocks[i] === undefined) this.textBlocks[i] = "";
            
            uploadPromise.then(async (imageUrls)=>{
                let createData = { textBlocks: this.textBlocks, imageUrls };
                if(!this.prevTreasure){
                    createData.latitude = this.position.latitude;
                    createData.longitude = this.position.longitude;
                }
                
                let fetchUrl = this.prevTreasure ? this.$endpoints.editTreasure(this.prevTreasure.id) : this.$endpoints.createTreasure;
                let request = await fetch(fetchUrl,{
                    credentials: 'include',
                    body: JSON.stringify(createData),
                    method: this.prevTreasure ? 'put' : 'post',
                    headers:{ 'Content-Type': 'application/json' }
                })
                let createFetchJson = await request.json();
                if(!request.ok){
                    console.log(request);
                    alert('Error occured. Check console for details');
                }else {

                    if(this.prevTreasure){
                        this.$emit('update-markers');
                        this.$emit('change-state',this.appStates.map);
                    }

                    //if(this.prevTreasure) location.reload(true);
                    this.fetchResultTreasure = createFetchJson;
                    this.showReward = true;
                    this.$emit('update-markers');
                    if(this.appState !== this.appStates.edit && this.appState !== this.appStates.create)
                        //User closed edit/creation overlay before all images were uploaded => unable to emit update-markers => force page reload
                        location.reload(true);
                }
            });
        },
        async uploadFiles(resolve){
            const imagePromises = this.images.map((file)=>{
                let img = new Image;
                img.src = URL.createObjectURL(file);

                return new Promise((imgResolve)=>{
                    img.onload = () => {
                        let rescaledCanvas = rescaleImgCanvas(img, Math.min(img.width, 450), Math.min(img.height, 450));
                        rescaledCanvas.toBlob( async (blob) =>{
                            
                            let data = new FormData();
                            file = new File([blob],file.name, {type: file.type});
                            data.append('file', file, file.name);
                            let uploadFetch = await fetch(this.$endpoints.uploadImage, {
                                credentials: 'include',
                                method: 'post',
                                body: data
                            });
                            if(!uploadFetch.ok){
                                console.log(uploadFetch);
                                alert('Error occured. Check console for details');
                            }
                            let uploadedResponse = await uploadFetch.json();
                            imgResolve( uploadedResponse.url );
                        })
                    }
                })
            })

            Promise.all(imagePromises).then((urls)=> resolve(urls));
        },
        fileSelect(arg){
            this.images.push(arg.file);
        },
        textInput(arg){
            let i = arg.index - 1;
            this.textBlocks[i] = arg.text;
        },
        deleteBlock(arg){
            let i = arg.index - 1;
            this.images = this.images.filter((img,index)=>{ return i != index; });
            this.textBlocks = this.textBlocks.filter((txt,index)=> i != index );
        }
    },
    computed: {
        disableButton(){
            return this.forceDisableButton || (this.imgFiles.length === 0 && this.prevTreasure);
        }
    }
}
</script>

<style scoped>
    .title-1,.title-2{
        line-height: 115%;
        font-weight: normal;
        text-align: left;
        margin-top: 20px;
        margin-bottom: 0;
    }
    .title-2{
        margin-top: 10px;
        color: #68676C;
    }
    .creation-overlay{
        display: flex;
        flex-direction: column;
    }
    .creation-overlay-padding{
        display: flex;
        flex-direction: column;
        padding: 0 2.5vw;
        height: 100%;
        width: 95vw;
    }
    .creation-overlay-container{
        overflow: auto;
        flex: 1;
    }
    .cancel-button{
        width: 8em;
        border: 0;
        text-align: left;
        font-family: inherit;
        font-size: 110%;
        margin-bottom: -15px;
        margin-left: -5px;
    }
</style>

