<template>
<div class="map-outer-container">
        <div class="map-container" >
            <WelcomePage 
            @show-map="showMap"
            v-if="this.appState === this.appStates.welcome" />
            <MapFrame class="map-frame"
            ref="mapFrame"
            :treasureTypes="this.treasureTypes"
            :class="{hide : (this.appState === this.appStates.welcome)}"
            :position="this.position"
            :bar="this.bar"
            :appState="this.appState"
            :appStates="this.appStates"
            :user="this.user"
            @change-state="changeState"
            @treasures-loaded="loadedTreasures"
            @open-creation-overlay="changeState(appStates.create)"
            @open-popup="openPopup"
            @geolocation-complete="geolocationComplete" />
            <div class="bar-container">
                <Popup
                v-if="showPopup"
                :user="this.user"
                :treasuresData="this.treasuresData"
                :popupData="this.popupData" 
                :position="this.position"
                :appState="this.appState"
                :appStates="this.appStates"
                :treasureTypes="this.treasureTypes"
                @change-state="changeState"
                @show-treasure-on-map="showTreasureOnMap"
                @update-popupData="updatePopupData"
                @update-treasure="updateTreasure"
                @update-markers="updateMarkers"
                @close-profile="changeState(appStates.map)"
                @close-create="changeState(appStates.map)"
                @close-popup="closePopup" />
                <MyBar
                ref="myBar"
                v-on:forget-me="logout"
                v-on:mounted="bar = $refs.myBar"
                @change-state="changeState"
                :user="user"
                :appState="this.appState"
                :appStates="this.appStates"
                v-if="user!=undefined" />
            </div>
        </div>
    <div class="offset"></div>
</div>
</template>

<script>
import Popup from '@/components/Popup.vue'
import MapFrame from '@/components/MapFrame.vue'
import MyBar from '@/components/MyBar.vue'
import WelcomePage from '@/components/WelcomePage.vue'
//import Vue from 'vue'

export default {
    name: 'myMap',
    props: ['user'],
    data(){
        const appStates = Object.freeze({
                "closed":0,
                "low":1,
                "high":2,
                "description":3,
                "digout":4,
                "create":5,
                "profile":6,
                "info":7,
                "map":8,
                "welcome":9,
                "edit":10,
                "users":11,
                "discussion":12,
                "place":13
            });
        return{
            treasuresData: undefined,
            popupData: undefined,
            position: undefined,
            bar: undefined,
            appStates: appStates,
            appState: appStates.welcome,
            treasureTypes: Object.freeze({"found":1,"created":2,"filtered":3,"user":4}),
            updateMarkersOnPopupClose: false,
            mapIsChecked: false
        }
    },
    components: {
        Popup, 
        MapFrame,
        MyBar,
        WelcomePage
    },
    methods: {
        loadedTreasures(treasuresData){
            this.$refs.myBar.setStats(treasuresData);
            this.treasuresData = treasuresData;
        },
        openPopup(popupData/* treasure treasureType treasureTypes */){
            this.changeState(this.appStates.low);
            this.popupData = popupData;
        },
        closePopup(){
            this.popupData=undefined;
            this.changeState(this.appStates.map);
        },
        geolocationComplete(position){
            this.position = position;
        },
        logout(){ fetch(this.$endpoints.logout, {method: 'get', credentials: 'include'}).then(()=>location.reload(true)); },
        async showMap(){
            this.changeState(this.appStates.map);
        },
        updatePopupData(popupData){
            this.$refs.mapFrame.updateTreasure(popupData.treasure);
            this.popupData = popupData;
        },
        updateTreasure(treasure){
            this.$refs.mapFrame.updateTreasure(treasure);
        },
        changeState(newState){
            if(this.updateMarkersOnPopupClose){
                this.updateMarkersOnPopupClose = false;
                this.updateMarkers();
            }
            //console.log("new:"+this.$enumNameByIndex(newState,this.appStates),"old:"+this.$enumNameByIndex(this.appState, this.appStates))
            if(this.appState === this.appStates.place || newState === this.appStates.place){
                this.popupData = undefined;
                this.$refs.mapFrame.displayUserLocation(this.appState === this.appStates.place);
            }
            this.appState = newState;
            this.waitForMapResize();
        },
        async waitForMapResize(){
            while (!this.mapIsChecked){
                let map = this.$map;
                await new Promise((resolve)=> setTimeout(resolve,10) )
                if(map){
                    let canvas = map._canvas;
                    if(canvas.style.height == '300px' && canvas.style.width == '400px'){
                        map.resize();
                    }
                    this.mapIsChecked = true;
                }
            }
        },
        updateMarkers(){
            this.$refs.mapFrame.loadTreasures(true);
        },
        showTreasureOnMap(lngLat){
            this.changeState(this.appStates.map);
            this.$map.flyTo({center: lngLat, zoom: 16});
        },
        updateMarkersOnNextPopup(){ this.updateMarkersOnPopupClose = true; }
    },
    computed:{
        showPopup(){
            return this.appState !== this.appStates.map && this.appState !== this.appStates.welcome;
        }
    }
}
</script>

<style>
    .bar-container{
        position: relative;
        height: calc(var(--vh, 1vh) * 12); /*  https://css-tricks.com/the-trick-to-viewport-units-on-mobile/  */
        width: 100%;
        z-index: 2;
        background-color: white;
    }
    .map-frame{
        height: calc(var(--vh, 1vh) * 88);
        width: 100%;
        position: relative;
    }
    .map-outer-container{
        height: 100%;
        width: 100%;
    }
    .map-frame > *{
        height: 100%;
        width: 100%;
        position: relative;
    }
    .hide{
        display: none;
    }
</style>