<template>
    <div class="discussion-container">
        <div>
            <h4 @click="goBack"></h4>
            <h3>{{this.isWriting ? "" : ""}}</h3>
        </div>
        <div v-if="isWriting" class="write-container">
            <div class="author-container">
                <img :src="$endpoints.userProfilePicture(user)" />
                <div class="author-info-container">
                    <span class="author-name">{{user.username}}</span>
                    <span>{{user.department}}</span>
                </div>
            </div>
            <textarea v-model="commentText" placeholder=" ...">

            </textarea>
        </div>
        <div v-if="!isWriting" class="comments-container">
            <div v-if="popupData.treasure.comments.length === 0" class="no-comments-tip">
                 ,   .
            </div>
            <div class="comment-container" :key="index" v-for="(comment, index) in popupData.treasure.comments">
                <div class="author-container">
                    <img :src="$endpoints.userProfilePicture(comment.author)" />
                    <div class="author-info-container">
                        <span class="author-name">{{comment.author.username}}</span>
                        <span>{{comment.author.department}}</span>
                    </div>
                </div>
                <span class="comment-text">
                    {{comment.text}}
                </span>
                <div class="comment-bottom-container">
                    <span class="date">
                        {{ new Intl.DateTimeFormat('ru', { year: 'numeric', month: 'long', day: '2-digit' }).format(new Date(comment.created)) }}
                    </span>
                    <div class="like-container" @click="likeComment(index,comment)">
                        <span class="like-count" :class="{active: isLiked(comment)}">
                            {{comment.likedIds.length}}
                        </span>
                        <button class="like-button" :class="{active: isLiked(comment)}" :disabled="disableLikes"></button>
                    </div>
                </div>
            </div>
        </div>
        <button @click="startWriting" :disabled="isWriting && commentText.length === 0" class="continue-button">{{ isWriting ? "" : " ? "}}</button>
    </div>
</template>

<script>
export default {
    name: 'discussionOverlay',
    props: ["appState","appStates","popupData","user"],
    data(){
        return{
            isWriting: false,
            commentText: '',
            disableLikes: false
        }
    },
    methods: {
        isLiked(comment){
            return !!comment.likedIds.find((id)=>id == this.user.id);
        },
        goBack(){
            if(this.isWriting) this.isWriting = false;
            else this.$emit("change-state", this.appStates.high);
        },
        startWriting(){
            if(!this.isWriting){
                this.isWriting = true;
            }else{
                fetch(this.$endpoints.postComment(this.popupData.treasure.id),{
                    method: 'post',
                    credentials: 'include',
                    body: this.commentText
                }).then((r)=> r.json().then((t)=>{ 
                    this.popupData.treasure = t; 
                    this.$emit('update-treasure',t);
                    this.isWriting = false; 
                    this.commentText = ''; 
                }) );
            }
        },
        likeComment(index,comment){
            let liked = this.isLiked(comment);
            if(liked){
                comment.likedIds = comment.likedIds.filter((id)=>id != this.user.id);
            }else{
                comment.likedIds.push(this.user.id);
            }
            fetch(this.$endpoints.likeComment(this.popupData.treasure.id, this.popupData.treasure.comments[index].id),{
                method: 'put',
                credentials: 'include'
            }).then((r)=>r.json((t)=>this.$emit('update-treasure',t))).finally(()=>this.disableLikes = false);
            this.disableLikes = true;
        }
    }
}
</script>

<style scoped>
