<template>
  <div class="popup-container" v-bind:class="this.$enumNameByIndex(this.appState,this.appStates)" @click.stop="">
    <div class="drag-sign"></div>
    <!--v-touch:swipe.self.top="tapHandler" v-touch:swipe.self.bottom="endHandler"-->
    <div class="drag-block" @touchmove="touchMove" @touchstart="touchStart" @touchend="touchEnd"></div>

    <CreateEditTreasureOverlay
      @change-state="changeState"
      @close-creation-overlay="$emit('close-create')"
      @update-markers="$emit('update-markers');"
      @success-creation-overlay="closeAndUpdate"
      v-if="this.showCreateEditTreasureOverlay || this.showEditOverlay"
      :position="position"
      :user="user"
      :prevTreasure="popupData ? popupData.treasure : undefined"
      :appState="appState"
      :appStates="appStates"
    />

    <ProfileOverlay
      v-if="this.showProfileOverlay"
      :treasureTypes="this.treasureTypes"
      :user="this.user"
      :treasuresData="this.treasuresData"
      @show-treasure-on-map="showTreasureOnMap"
    />
    <DescriptionOverlay
      v-if="this.showDescriptionOverlay"
      :activeSlideIndex="this.descriptionIndex"
      :popupData="this.popupData"
    />
    <DigoutOverlay
      v-if="this.showDigoutOverlay"
      @success-digout="closeAndUpdate"
      @update-markers="$emit('update-markers');"
      :digoutOverlayData="this.popupData"
    />
    <!--<EditOverlay 
      v-if="this.showEditOverlay"
      :popupData="this.popupData"
      :user="this.user"
      :treasureTypes="this.treasureTypes"
      :appState="this.appState"
      :appStates="this.appStates"
    />-->

    <UsersOverlay 
      v-if="this.showUsersOverlay"
      @change-state="changeState"
      
      :likedList="this.popupData.treasure.likedIds"
      :uid="this.user.id"
      :popupData="this.popupData" 
      :appState="this.appState"
      :appStates="this.appStates"
    />

    <InfoOverlay v-if="this.showInfoOverlay" />
    <DiscussionOverlay 
    :appState="this.appState"
    :appStates="this.appStates"
    :popupData="this.popupData"
    :user="this.user"
    @change-state="changeState"
    @update-treasure="updateTreasure"
    v-if="this.showDiscussionOverlay" />
    <span v-if="this.appState === this.appStates.place" class="place-tip">     ,    </span>
    <div class="popup-info" v-if="this.appState === this.appStates.high || this.appState === this.appStates.low" >
      <div class="popup-info-line-container">
        <span class="popup-info-line">
          <span class="popup-info-line field-name">:</span>
          {{this.popupData.treasure.formattedLocation}}
        </span>
        <!--<button @click="appState=appStates.high">c</button>-->
      </div>
      <div v-if="this.appState === this.appStates.high" class="popup-info-line-container">
        <div class="activity-container">
          <div @click="openUsersOverlay">
            <button :disabled="popupData.treasure.userFindings.length === 0" class="activity-button users">{{popupData.treasure.userFindings.length}}</button>
          </div>
          <div @click="likeButtonClick">
            <!--:class:"{canLike: canLike}"-->
            <button class="activity-button likes canLike" :class="{liked: liked}" :disabled="isPendingRequest">{{popupData.treasure.likedIds.length}}</button>
          </div>
        </div>
        <span  class="popup-info-line">
          <span class="popup-info-line field-name">:</span>
          {{this.popupData.treasure.author.username}}
        </span>
      </div>
      <carousel
        ref="carousel"
        class="popup-image-list"
        v-if="this.appState === this.appStates.high"
        :paginationEnabled="false"
        :navigateTo="/*this.descriptionIndex == undefined ? 0 : this.descriptionIndex*/0"
        :perPage="1">
        <slide
          class="popup-square-image-container"
          v-for="index in popupData.treasure.imageUrls.length"
          :key="index">
            <img
            :data-key="index"
            @click="imgClicked"
            :src="$endpoints.imageByUrl(popupData.treasure.imageUrls[index-1])" />
        </slide>
      </carousel>
      <!--<span v-if="this.appState === this.appStates.high" class="description-tip black">{{this.popupData.treasure.textBlocks[0]}}</span>
      <span v-if="this.appState === this.appStates.high" class="description-tip">  ,   .</span>-->
      <span v-if="this.appState === this.appStates.high" class="description-tip">  ,   .</span>
      <div class="disc-edit-container">
        <input type="button" @click.prevent="openDiscussionOverlay" value="">
        <input type="button" @click.prevent="openEditOverlay" v-if="this.popupData.treasureType === this.popupData.treasureTypes.created" value="">
      </div>
      <button
        class="continue-button"
        :disabled="this.isDigoutDisabled"
        @click="digoutClick"
      >{{this.buttonText}}</button>
    </div>
    <!--<button class="popup-close-button" @click="$emit('close-popup')">X</button>-->
  </div>
</template>

<script>
import Vue from "vue";
import { Carousel, Slide } from "vue-carousel";
import DescriptionOverlay from "@/components/DescriptionOverlay.vue";
import DigoutOverlay from "@/components/DigoutOverlay.vue";
import ProfileOverlay from "@/components/ProfileOverlay.vue";
import CreateEditTreasureOverlay from "@/components/CreateEditTreasureOverlay.vue";
import InfoOverlay from "@/components/InfoOverlay.vue";
import DiscussionOverlay from "@/components/DiscussionOverlay.vue";
import UsersOverlay from "@/components/UsersOverlay.vue";

export default {
  name: "popup",
  //.treasure, .treasureType, .treasureTypes
  props: [
    "popupData",
    "user",
    "treasuresData", //Data about user treasures
    "position",
    "appState",
    "appStates",
    "treasureTypes"
  ],
  components: {
    Carousel,
    Slide,
    DescriptionOverlay,
    DigoutOverlay,
    ProfileOverlay,
    CreateEditTreasureOverlay,
    InfoOverlay,
    DiscussionOverlay,
    UsersOverlay
  },
  data() {
    return {
      descriptionIndex: undefined,
      imgMouseIsUp: true,
      beforeDigoutState: undefined,
      swipeData: { startY: undefined, lastY: undefined },
      canLike: true,
      isPendingRequest: false
    };
  },
  methods: {
    updateTreasure(treasure){
      this.$emit('update-treasure',treasure);
    },
    touchStart(e) {
      this.swipeData.startY = e.touches[0].pageY;
    },
    touchMove(e) {
      this.swipeData.lastY = e.touches[0].pageY;
    },
    touchEnd(forceDown) {
      let isUp = this.swipeData.lastY < this.swipeData.startY;

      if (isUp) {
        //Swipe up
        if (this.appState === this.appStates["low"])
          this.$emit("change-state", this.appStates["high"]);
      } else if (!isUp || forceDown === true) {
        //Swipe down
        let newState;
        let st = this.appState;
        let sts = this.appStates;
        let context = this;
        let closePopup = function() {
          newState = sts.closed;
          context.emitAfterAnimation("close-popup");
        };
        if (st === sts.high) 
          newState = sts.low;
        else if (st === sts.description || st === sts.users || st === sts.discussion) {
          newState = sts.high;
        } else if (st === sts.digout) {
          newState = this.appStates.map;
          //newState = this.beforeDigoutState;
          //if(newState==undefined){console.error('Closing digout menu, previous state is undefined'); newState = this.appStates["high"];}
        } else if (
            st === sts.profile || 
            st === sts.create || 
            st === sts.info || 
            st === sts.edit ||
            st === sts.low || 
            st === sts.place) {
          closePopup();
        } else {
          console.error(
            `Swipe down: Unsupported appState ${this.appState}`,
            this.appStates
          );
          return;
        }
        this.$emit("change-state", newState);
      }
    },
    changeState(state){
      this.$emit("change-state", state);
    },
    imgClicked(e) {
      let el = e.target;
      this.descriptionIndex = parseInt(el.dataset.key) - 1;
      this.$emit("change-state", this.appStates["description"]);
    },
    digoutClick() {
      this.beforeDigoutState = this.appState;
      this.$emit("change-state", this.appStates["digout"]);
    },
    openCreateEditTreasureOverlay() {
      this.$emit("change-state", this.appStates["create"]);
    },
    openEditOverlay(){
      this.$emit("change-state",this.appStates["edit"]);
    },
    openDiscussionOverlay(){
      this.$emit("change-state", this.appStates["discussion"]);
    },
    emitAfterAnimation(event, ...args) {
      setTimeout(() => this.$emit(event, args), 300);
    },
    closeAndUpdate() {
      //console.log("Closing and updating")
      //if(this.appState === this.appStates.digout) this.$emit('change-state',this.appStates.low);
      Vue.nextTick(() => {
        this.$emit("change-state", this.appStates.map);
      });
      this.$emit("update-markers");
    },
    showTreasureOnMap(lngLat) {
      this.$emit("show-treasure-on-map", lngLat);
    },
    async likeButtonClick(){
      //if(!this.canLike) return;
      fetch(this.$endpoints.likeTreasure(this.popupData.treasure.id), { method: 'PUT', credentials: 'include' })
      .then((response)=>response.json().then((treasure)=> {
        let clonedData = JSON.parse(JSON.stringify(this.popupData));
        clonedData.treasure = treasure;
        this.$emit('update-popupData',clonedData);
      }))
      .finally(()=>this.isPendingRequest = false);
      this.isPendingRequest = true;
    },
    openUsersOverlay(){
      this.$emit("change-state", this.appStates.users)
    },
  },
  computed: {
    showDescriptionOverlay() {
      return this.appState === this.appStates["description"];
    },
    showDigoutOverlay() {
      return this.appState === this.appStates["digout"];
    },
    showProfileOverlay() {
      return this.appState === this.appStates["profile"];
    },
    showCreateEditTreasureOverlay() {
      return this.appState === this.appStates["create"];
    },
    showInfoOverlay() {
      return this.appState === this.appStates["info"];
    },
    showEditOverlay(){
      return this.appState === this.appStates["edit"];
    },
    showDiscussionOverlay(){
      return this.appState === this.appStates["discussion"];
    },
    showUsersOverlay(){
      return this.appState === this.appStates["users"];
    },
    isDigoutDisabled() {
      return (
        this.popupData.treasureType !== this.popupData.treasureTypes.filtered
      );
    },
    liked(){
      return !!this.popupData.treasure.likedIds.find((id)=>id === this.user.id);
    },
    buttonText() {
      switch (this.popupData.treasureType) {
        case this.popupData.treasureTypes.filtered:
          return "   ";
        case this.popupData.treasureTypes.created:
          return "   ";
        case this.popupData.treasureTypes.found:
          return "   ";
        default:
          return "";
      }
    }
  }
};
</script>

<style>
.reward-info-container {
  position: relative;
  top: 35%;
  transform: translateY(-50%);
}
.reward-info-container > p {
  margin: 0;
}
