  font-size: 3em;
  margin-right: 0.25em;
  line-height: 90%;
  margin-bottom: 5px;
}
.confirm-button-container {
  position: absolute;
  bottom: 0;
  padding: 0 3%;
  width: 94%;
}
.confirm-button-container > a {
  display: block;
  margin-top: 10px;
  text-align: left;
  text-decoration: none;
  color: #475deb;
}
.screen-overlay {
