.drag-sign {
  margin: 2vh;
  width: 15vw;
  height: 0.5vh;
  border-radius: 25px;
  margin-left: auto;
  margin-right: auto;
  position: relative;
  background-color: lightgray;
}
.drag-block {
  position: absolute;
  top: -1.5em;
  width: 100%;
  height: 3em;
  padding-bottom: 1.5em;
  z-index: 2;
  /*background-color: rgba(0, 0, 0, 0.5);*/
}
.popup-container.high > .drag-block, .popup-container.low > .drag-block {
  height: 4em !important;
}
.popup-container {
  z-index: 4;
  position: absolute;
  margin: 0;
  padding: 0;
  bottom: calc(var(--vh, 1vh) * 12);
  border-radius: 25px 25px 0 0;
  touch-action: none;
  width: 100%;
  background-color: white;
  transition: all 0.3s;
}
.popup-container.closed {
  max-height: 5vh;
  min-height: 3vh;
}
.popup-container.low {
  min-height: 15vh;
  max-height: calc(var(--vh, 1vh) * 88);
}
.popup-container.high {
  min-height: 35vh;
  max-height: calc(var(--vh, 1vh) * 88);
}
.popup-container.description,
.popup-container.digout,
.popup-container.create,
.popup-container.profile,
.popup-container.create,
.popup-container.users,
.popup-container.info,
.popup-container.edit,
.popup-container.discussion {
  min-height: 70vh;
  max-height: calc(var(--vh, 1vh) * 88);
}
@media only screen and (min-width: 640px) {
  .popup-square-image-container img {
    min-width: 10%;
  }
}
.popup-info {
  position: relative;
  width: calc(100% - 2em);
  margin: 1em 0 0 1em;
  display: block;
  background-color: white;
}
.popup-info-line-container {
  height: auto;
  margin-left: 5px;
  margin-top: 5px;
  margin-bottom: 0;
  width: 100%;
  text-align: left;
  display: block;
}
.popup-info-line {
  font-size: 90%;
  text-overflow: ellipsis;
}
.popup-image-list {
  position: relative;
  height: 50%;
  width: 100%;
  display: block;
  white-space: nowrap;
}
.description-tip.black {
  color: black;
}
.description-tip {
  margin-top: 5px;
  color: #c4c4c4;
  overflow: hidden;
  white-space: nowrap;
  display: block;
  /*text-align: left;*/
}

