<template>
<div ref="container">
    <!--v-bind:class="{ hide : this.showCreateEditTreasureOverlay }"-->
    <mapbox
        v-if="readyToLoadMap"
        access-token="pk.eyJ1IjoicGV0ZXJkb25za295IiwiYSI6ImNrN3JueGlyejA1anMzaHBjdDNuNmRkaWQifQ.6M3909y1_g9-0UXQS3nRHg"
        nav-control="{
            show: false, 
            position: 'top-right'
        }"
        v-bind:map-options="{
            style: 'mapbox://styles/mapbox/streets-v11',
            center: [14, 51],
            zoom: position != undefined ? 15 : 2
        }"
        @load="loaded"/>
    <div class="hide-all">
        <div 
        ref="userMarker" 
        class="mymarker user icon"></div>
    </div>
</div>
</template>

<script>
import Mapbox from 'mapbox-gl-vue'
import mapboxgl from 'mapbox-gl'
import MapboxGeocoder from '@mapbox/mapbox-gl-geocoder'
import Vue from 'vue'

export default {
    name: 'mapFrame',
    props: ['bar','appState','appStates','treasureTypes','user'],
    data(){
        return{
            foundTreasures: undefined,
            createdTreasures: undefined,
            filteredTreasures: undefined,
            position: undefined,
            MyControl: class MyControl{
                constructor(container){
                    this.container = container;
                }
                onAdd(map){
                    this.map = map;
                    return this.container;
                }
                onRemove(){
                    this.container.parentNode.removeChild(this.container);
                    this.map = undefined;
                }
            }
        }
    },
    components: {
        Mapbox, 
    },
    mounted(){
        this.$options.createdMarkers = []; this.$options.foundMarkers = []; this.$options.filteredMarkers = []; this.$options.allMarkers = [];
        if(this.$updateMsInterval) setInterval(() =>{ this.loadTreasures(true);}, this.$updateMsInterval );
        this.loadTreasures(false);
    },
    methods: {
        updateTreasure(treasure){
            let treasureType = this.determineTreasureType(treasure);
            let array;
            switch(treasureType){
                case this.treasureTypes.created: array = this.createdTreasures; break;
                case this.treasureTypes.found: array = this.foundTreasures; break;
                case this.treasureTypes.filtered: array = this.filteredTreasures; break;
            }
            for (let i = 0; i < array.length; i++) 
                if(array[i].id === treasure.id){
                    //console.log("Updating ",array[i],"to",treasure)
                    //console.log(this.foundTreasures)
                    array[i] = treasure; 
                    //console.log(this.foundTreasures)
                    break;
                }
        },
        determineTreasureType(treasure){
            if(treasure.author.id === this.user.id) return this.treasureTypes.created;
            if(treasure.userFindings.filter((fu)=>fu.user.id === this.user.id).length === 1) return this.treasureTypes.found;
            return this.treasureTypes.filtered;
        },
        updateMarkers(map){
            //console.log('am',this.allMarkers);
            //console.log(this.$options.foundMarkers,this.$options.createdMarkers,this.$options.filteredMarkers);
            this.$options.allMarkers.forEach((m)=>{
                m.remove();
            });
            this.foundTreasures.forEach((t)=>{
                this.createMarker(t,this.treasureTypes.found,map);
            });
            this.createdTreasures.forEach((t)=>{
                this.createMarker(t,this.treasureTypes.created,map);
            });
            this.filteredTreasures.forEach((t)=>{
                this.createMarker(t,this.treasureTypes.filtered,map);
            });
        },
        createMarker(treasure, treasureType, map){
            let el = document.createElement('div');
            let addToMarker;
            //el.innerHTML = 'O';
            el.classList.add('mymarker');
            switch (treasureType) {
                case this.treasureTypes.found:
                    el.classList.add('found');
                    addToMarker = (m) => this.$options.foundMarkers.push(m);
                    //console.log(treasure.id+" is found");
                    break;

                case this.treasureTypes.created:
                    el.classList.add('created');
                    addToMarker = (m) => this.$options.createdMarkers.push(m);
                    //console.log(treasure.id+" is created");
                    break;

                case this.treasureTypes.filtered:
                    el.classList.add('filtered');
                    addToMarker = (m) => this.$options.filteredMarkers.push(m);
                    //console.log(treasure.id+" is filtered");
                    break;
                case this.treasureTypes.user:
                    el.classList.add('container','user');
                    //console.log(treasure.id+" is user location");
                    break;
                default:
                    throw new Error("Unrecognized treasureType "+treasureType);
            }
            let loc;
            if(treasureType!==this.treasureTypes.user){
                el.dataset.treasureId = treasure.id;
                el.addEventListener('click', () => this.onMarkerClick(el) );
                loc = [treasure.longitude, treasure.latitude];
            }
            else{
                //let userMarker = this.$refs.userMarker;
                //el.appendChild(userMarker);
                loc = [this.position.longitude, this.position.latitude];
            }
            el.dataset.treasureType = treasureType;
            let m = new mapboxgl.Marker(el)
                    .setLngLat(loc)
                    .addTo(map);
            if(addToMarker) addToMarker(m);
            this.$options.allMarkers.push(m);
            return m;
        },
        async loadTreasures(reload){
            this.$options.allMarkers.forEach(m =>{ m.remove(); });

            let curUserTreasuresFetch = await fetch(this.$endpoints.userTreasures(this.user.id),{credentials: "include"});
            let curUserTreasures = await curUserTreasuresFetch.json();
            this.foundTreasures = curUserTreasures.foundTreasures == undefined ? [] : curUserTreasures.foundTreasures;
            this.createdTreasures = curUserTreasures.createdTreasures == undefined ? [] : curUserTreasures.createdTreasures;
            
            let allTreasuresFetch = await fetch(this.$endpoints.treasures,{credentials: "include"});
            let allTreasures = await allTreasuresFetch.json();

            let filterId = [];

            this.foundTreasures.forEach(t => filterId.push(t.id));
            this.createdTreasures.forEach(t => filterId.push(t.id));

            if(allTreasures!=undefined) this.filteredTreasures = allTreasures.filter((t)=>{
                try{ filterId.forEach((id)=>{ if(t.id===id) throw new Error('breaking'); }); }
                catch(e){ return false; }
                return true;
            });
            else this.filteredTreasures = [];

            if(reload) this.updateMarkers(this.$map);

            this.$emit('treasures-loaded',this.treasuresData);
        },
        displayUserLocation(hide){
            this.$refs.container.style.setProperty('--displayLoc',hide ? 'none' : 'block');
        },
        loaded(map){
            let geocoderControl = new MapboxGeocoder({ accessToken: mapboxgl.accessToken, mapboxgl: mapboxgl });
            Vue.nextTick(()=>{
                let container = geocoderControl.container;
                container.classList.add('expand'); 
                //container.addEventListener('click',()=>container.classList.add('expand'))
            });
            map.addControl(geocoderControl,'top-left');

            let findControlBtn = document.createElement('button');
            findControlBtn.addEventListener('click',()=>this.findClick());
            let geolocateControl = new mapboxgl.GeolocateControl({
                trackUserLocation: false,
                //showUserLocation: false
            });
            geolocateControl.onAdd(map);
            let markerClickEl = document.createElement('div');
            markerClickEl.dataset.treasureType = this.treasureTypes.user;
            //let context = this;
            /*let moveendListener = () => {
                //if(!map.getBounds().contains(context.$userMarker.getLngLat())){context.$userMarker.getElement().style.display = 'none'; return;}
                let idleListener = ()=> {
                    //context.$userMarker.getElement().style.display = 'none';
                    //context.onMarkerClick(markerClickEl);
                    map.off('moveend', moveendListener);
                    map.off('idle', idleListener);
                };
                map.on('idle', idleListener);
            };*/
            geolocateControl.on('geolocate',(/*e*/)=>{
                this.$emit('change-state',this.appStates.place);
                //this.position = {longitude : e.coords.longitude,latitude: e.coords.latitude};
                //this.$emit('geolocation-complete', this.position);
                //if(this.$userMarker!=undefined) this.$userMarker.remove();
                //this.$userMarker = this.createMarker(undefined, this.treasureTypes.user, map);
                //map.on('moveend', moveendListener); 
                //this.$userMarker.getElement().style.display = '';
            });
            let startTime;
            let startPoint;
            const startTreasureCreation = (lat, lng) => {
                this.$emit('geolocation-complete', {latitude: lat, longitude: lng});
                Vue.nextTick(()=>this.$emit('open-creation-overlay'));                
            }
            // NOTE: mousedown and mouseup might not get trigger in firefox responsive design mode. You need to zoom in on the map and slightly move the cursor right after holding down the mouse button in order for the event to fire
            const handleMapEvent = (e) => {
                console.log(e.type+", placeState: "+(this.appState === this.appStates.place));
                if(["touchstart", "mousedown"].includes(e.type)){
                    startTime = new Date();
                    startPoint = e.point;
                }else if(["touchend", "mouseup"].includes(e.type) && this.appState === this.appStates.place){
                    const timeDif = new Date() - startTime;
                    const xDif = startPoint.x - e.point.x;
                    const yDif = startPoint.y - e.point.y;
                    console.log({xDif, yDif, timeDif})
                    if(timeDif > 750 && Math.abs(xDif) < 25 && Math.abs(yDif) < 25){
                        startTreasureCreation(e.lngLat.lat, e.lngLat.lng)
                    }
                }
            };
            map.on('mouseup', handleMapEvent);
            map.on('mousedown', handleMapEvent);
            map.on('touchend',handleMapEvent);
            map.on('touchstart',handleMapEvent);
            //geolocateControl.on('trackuserlocationend',()=>{if(this.$userMarker!=undefined)this.$userMarker.remove();});
            setTimeout(()=>{
                this.bar.addControls(findControlBtn, geolocateControl._geolocateButton);
            },1000); //TODO: Better way of waiting for elements to load
            Vue.prototype.$map = map;
            this.updateMarkers(map);
        },
        findClick(){
            this.$emit('change-state',this.appStates.map);
            this.$map.flyTo({center: {lng: 14, lat: 51}, zoom: 2});
        },
        onMarkerClick(el){
            if(el.dataset.treasureType == this.treasureTypes.user){
                this.$emit('open-creation-overlay');
            }
            else{
                let treasure = this.treasureById(el.dataset.treasureId);
                let treasureType = parseInt(el.dataset.treasureType);
                let popupData = {
                    treasure:treasure,
                    treasureType:treasureType,
                    treasureTypes:this.treasureTypes
                };

                this.$map.flyTo({
                    center: [treasure.longitude, treasure.latitude],
                    essential: true,
                    zoom: 15
                });
                this.$emit('open-popup',popupData);
            }
        },
        treasureById(id){
            let ret = null;
            this.allTreasures.forEach( t => { ret = t.id == id ? t : ret;} )
            return ret;
        },
        /*Generic*/sleep(ms) { return new Promise(resolve => setTimeout(resolve, ms)); }
    },
    computed: {
        allTreasures: {
            get(){
                let ret = [
                    ...this.foundTreasures == undefined ? [] : this.foundTreasures,
                    ...this.createdTreasures == undefined ? [] : this.createdTreasures,
                    ...this.filteredTreasures == undefined ? [] : this.filteredTreasures
                ];
                //console.log('ret '+ret.length,'trs '+(this.foundTreasures.length+this.createdTreasures.length+this.filteredTreasures.length));
                return ret;
            },
            cache: false           
        },
        readyToLoadMap(){
            return this.foundTreasures != undefined && this.createdTreasures != undefined && this.filteredTreasures != undefined;
        },
        treasuresData(){
            /*
            Too lazy to make it work
            const _allTreasures = this.allTreasures;
            const _foundTreasures = () => _allTreasures.filter((t)=>t.userFindings.filter((u)=>u.user.id === this.user.id).length === 1);
            const _createdTreasures = () => _allTreasures.filter((t)=>t.author.id === this.user.id);
            return{
                foundTreasures: _foundTreasures,
                createdTreasures: _createdTreasures,
                filteredTreasures: () => _allTreasures.filter((t)=> !_foundTreasures().includes(t) && !_createdTreasures().includes(t) ),
                allTreasures: _allTreasures
            }
            */
            return{
                foundTreasures: this.foundTreasures,
                createdTreasures: this.createdTreasures,
                filteredTreasures: this.filteredTreasures,
                allTreasures: this.allTreasures
            }
        }
    }
}
</script>

<style>
    .mapboxgl-ctrl-geocoder.mapboxgl-ctrl{
        width: 48px !important;
        min-width: 48px !important;
        border-radius: 50% !important;
        transition: none !important;
    }
    .mapboxgl-ctrl-geocoder--input{
        padding: 6px 45px 6px 40px !important;
    }
    .mapboxgl-ctrl-geocoder.mapboxgl-ctrl > svg{
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
    }
    .mapboxgl-ctrl-geocoder.mapboxgl-ctrl.expand > svg{
        top: 50%;
        left: 7%;
        transform: translate(-50%,-50%);
    }
    .mapboxgl-ctrl-geocoder.mapboxgl-ctrl.expand{
        width: 100% !important;
        min-width: 240px !important;
        border-radius: 25px !important;
    }
    .mapboxgl-user-location-dot, .mapboxgl-user-location-accuracy-circle{
        display: var(--displayLoc, block);
    }
    @media only screen and (min-width: 640px) {
        .mapboxgl-ctrl-geocoder.mapboxgl-ctrl{
            width: 36px !important;
            min-width: 36px !important;
        }
        .mapboxgl-ctrl-geocoder.mapboxgl-ctrl.expand{
            width: 33.3333% !important;
            min-width: 250px !important;
        }
    }
    .mymarker {
        height: 37px;
        width: 30px;
        cursor: pointer;
        z-index: 1;
    }
    .mymarker.filtered{
        background-image: url("data:image/svg+xml,%3Csvg width='30' height='37' viewBox='0 0 30 37' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M15 0C7.125 0 0 6.05463 0 15.4186C0 21.398 4.59375 28.4304 13.7625 36.5346C14.475 37.1551 15.5437 37.1551 16.2563 36.5346C25.4062 28.4304 30 21.398 30 15.4186C30 6.05463 22.875 0 15 0Z' fill='%23475DEB'/%3E%3C/svg%3E%0A");
    }
    .mymarker.created{
        background-image: url("data:image/svg+xml,%3Csvg width='30' height='37' viewBox='0 0 30 37' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M15 0C7.125 0 0 6.05463 0 15.4186C0 21.398 4.59375 28.4304 13.7625 36.5346C14.475 37.1551 15.5437 37.1551 16.2563 36.5346C25.4062 28.4304 30 21.398 30 15.4186C30 6.05463 22.875 0 15 0Z' fill='%2300A460'/%3E%3C/svg%3E%0A");
    }
    .mymarker.found{
        background-image: url("data:image/svg+xml,%3Csvg width='30' height='37' viewBox='0 0 30 37' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M15 0C7.125 0 0 6.05463 0 15.4186C0 21.398 4.59375 28.4304 13.7625 36.5346C14.475 37.1551 15.5437 37.1551 16.2563 36.5346C25.4062 28.4304 30 21.398 30 15.4186C30 6.05463 22.875 0 15 0Z' fill='%2368676C'/%3E%3C/svg%3E%0A");
    }
    .mymarker.user{
        z-index: 0;
    }
    .mymarker.user.icon{
        background-image: url("data:image/svg+xml,%3Csvg width='30' height='37' viewBox='0 0 30 37' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M15 0C7.125 0 0 6.05463 0 15.4186C0 21.398 4.59375 28.4304 13.7625 36.5346C14.475 37.1551 15.5437 37.1551 16.2563 36.5346C25.4062 28.4304 30 21.398 30 15.4186C30 6.05463 22.875 0 15 0Z' fill='%23FF645A'/%3E%3C/svg%3E%0A");
        /*background-color: #FF645A;
        border-radius: 50%;*/
    }
    .hide-all{
        display: none;
    }
    .hide-all *{
        display: none;
    }
</style>