<template>
    <div class="screen-overlay" @click="stopPropagation">
        <!--<div class="close-button-container">
            <button ref="closeButton" @click="$emit('close-description-overlay')">X</button>
        </div>-->
        <div class="description-container">
            <!--<div v-if="this.userCount > 0" style="display: block; height: 2em;">
                <span style="margin-top: 0.25em; font-size: 1.25em; display: block;">Found by {{this.userCount}} user{{this.userCount>1 ? 's' : ''}}</span>
            </div>-->
            <carousel
            :perPage="1"
            :navigateTo="this.activeSlideIndex"
            :navigation-enabled="true"
            :navigationNextLabel="'>'"
            :navigationPrevLabel="'<'"
            :adjustableHeight="true"
            :paginationEnabled="false">
                <slide
                class="description-screen-container"
                ref="slide"
                v-for="index in descriptionBlocks.length"
                :key="index">
                    <DescriptionBlock 
                    :blockData="descriptionBlocks[index-1]" />
                </slide>
            </carousel>
        </div>
    </div>
</template>

<script>
import DescriptionBlock from '@/components/DescriptionBlock.vue'
import {Carousel, Slide} from 'vue-carousel'

export default {
    name: 'descriptionOverlay',
    props: ['popupData','activeSlideIndex'],
    components: {
        DescriptionBlock,
        Carousel,
        Slide
    },
    methods: {
        stopPropagation(event){
            event.stopPropagation();
        }
    },
    computed: {
        descriptionBlocks(){
            let ret = [];
            const treasure = this.popupData.treasure;
            let totalBlocks = Math.max(treasure.textBlocks.length, treasure.imageUrls.length);
            for (let i = 0; i < totalBlocks; i++) {
                let imgUrl = treasure.imageUrls[i];
                let tBlock = treasure.textBlocks[i];
                if(imgUrl) ret.push({textBlock:tBlock,imgUrl:imgUrl,key:i});
            }
            return ret;
        },
        userCount(){ return this.popupData.treasure.userFindings.length; }
    }
}
</script>
<style>
.description-container > .VueCarousel{
    height: 100% !important;
    overflow: auto;
}
.VueCarousel-navigation{
    position: absolute;
    width: calc( 100% - 0.1em);
    top: 50% /*calc( var( --dvh, -100%) * 0.5 )*/;
}
.VueCarousel-navigation--disabled{
    display: none;
}
.VueCarousel-navigation-prev{
    left: 1.4em !important;
}
.VueCarousel-navigation-next{
    right: 1.4em !important;
}
.VueCarousel-navigation-button{
    font-size: 1.25em;
}
</style>
<style scoped>
    .description-screen-container{
        white-space: nowrap;
        overflow: auto;
        flex: none;
        display: block;
        height: 100%;
        --sidePadding: 4em;
        width: calc( 100% - var(--sidePadding) );
        padding: 0 calc( var( --sidePadding ) * 0.5);
    }
    .description-overlay{
        position: relative;
        height: calc( 75vh - 3.5em );
        width: 100%;
    }
    .description-container{
        width: 100%;
        margin-left: auto;
        margin-right: auto;
        height: 100%;
        position: relative;
    }
    .user-count{
        margin-left: auto;
        margin-right: auto;
        display: block;
    }

</style>