<template>
    <div ref="blockContainer">
        <!--Hidden-->
        <input 
        ref="file"
        :id="this.idPrefix" 
        :disabled="disableButton"
        @change="fileSelected($event.target)" 
        type="file" 
        accept="image/*">
        
        <label 
        v-if="this.imageSrc == undefined"
        class="img-scale round-border inner-border" 
        ref="label" 
        :for="this.idPrefix" >
            <svg width="57" height="51" viewBox="0 0 38 34" fill="none" xmlns="http://www.w3.org/2000/svg">
                <path d="M34.6957 8.09524H29.4583L26.4348 4.85714H16.5217V8.09524H24.9809L28.0043 11.3333H34.6957V30.7619H8.26087V16.1905H4.95652V30.7619C4.95652 32.5429 6.44348 34 8.26087 34H34.6957C36.513 34 38 32.5429 38 30.7619V11.3333C38 9.55238 36.513 8.09524 34.6957 8.09524ZM13.2174 21.0476C13.2174 25.5162 16.9183 29.1429 21.4783 29.1429C26.0383 29.1429 29.7391 25.5162 29.7391 21.0476C29.7391 16.579 26.0383 12.9524 21.4783 12.9524C16.9183 12.9524 13.2174 16.579 13.2174 21.0476ZM21.4783 16.1905C24.2043 16.1905 26.4348 18.3762 26.4348 21.0476C26.4348 23.719 24.2043 25.9048 21.4783 25.9048C18.7522 25.9048 16.5217 23.719 16.5217 21.0476C16.5217 18.3762 18.7522 16.1905 21.4783 16.1905ZM8.26087 8.09524H13.2174V4.85714H8.26087V0H4.95652V4.85714H0V8.09524H4.95652V12.9524H8.26087V8.09524Z" fill="#C4C4C4"/>
            </svg>
        </label>
        <div v-if="this.imageSrc" class="img-scale container">
            <span ref="delBtn" class="delete-button" @click="deleteBlock"></span>
            <img ref="img" class="img-scale round-border" @load="imgLoad" :src="this.imageSrc">
            <!--@click="$refs.file.click()"-->
        </div>
        
        <textarea 
        ref="description"
        class="round-border inner-border"
        :value="textBlocks[index-1]"
        :placeholder="this.imageSrc == undefined ? ' ' : /*' ...'*/ ''"
        @input="$emit('text-input',{index:index, text: $event.target.value})"
        :disabled="this.imageSrc == undefined || disableButton"
        spellcheck="false"></textarea>
    </div>
</template>

<script>
//import Vue from 'vue';

export default {
    name: 'creationBlock',
    props: ['index','totalBlocks','prevData','images','textBlocks','disableButton'],
    data(){
        return{
            idPrefix: 'file-'+this.index,
            showImagePlaceholder: this.prevData == undefined,
            imageSrc: undefined
        }
    },
    watch: {
        images: {
            handler(n){this.processImageArray(n); },
            deep: true
        }
    },
    mounted(){
        this.processImageArray(this.images);
    },
    methods: {
        processImageArray(imageArray){
            let i = this.index - 1;
            if(imageArray.length == i) {
                //last block
                this.imageSrc = undefined;
                this.$refs.file.value = null;
                return;
            }
            let fr = new FileReader();
            fr.onload = () => { 
                let img = new Image();
                this.imageSrc = fr.result;
                img.src = this.imageSrc;
            };
            fr.readAsDataURL(imageArray[i]);
        },
        imgLoad(e){
            let el = e.target;
            let blockContainer = this.$refs.blockContainer;
            let dif = (blockContainer.offsetHeight - el.offsetHeight - this.$refs.delBtn.offsetHeight) * 0.5;
            blockContainer.style.setProperty("--ih",dif+"px");
            //console.log("setting dif to "+dif+"px");
        },
        fileSelected(el){
            let file = el.files[0];
            if(file == undefined) return;
            el.nextSibling.innerHTML = file.name;
            this.$emit('file-select',{index: this.index, file: file, isEdit: this.imageSrc != undefined});
        },
        deleteBlock(){
            if(this.disableButton) return;
            this.$emit('delete-block',{index: this.index});
        }
    },
    computed: {
        blockData(){
            return {text: this.textarea, file: this.file};
        }
    }
}
</script>

<style scoped>
.delete-button{
    height: 30px;
    width: 30px;
    position: absolute;
    top: var(--ih, 0);
    right: 0;
    z-index: 2;
    object-fit: contain;
    background-repeat: no-repeat;
    background-image: url("data:image/svg+xml,%3Csvg width='35' height='35' viewBox='0 0 25 25' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Ccircle cx='12' cy='12' r='7' fill='white'/%3E%3Cpath d='M9.64296 8.46447L8.46444 9.64298L10.8215 12L8.46444 14.357L9.64296 15.5355L12 13.1785L14.357 15.5355L15.5355 14.357L13.1785 12L15.5355 9.64298L14.357 8.46447L12 10.8215L9.64296 8.46447Z' fill='%2368676C'/%3E%3Cpath d='M6.1074 6.10744C2.85824 9.3566 2.85824 14.6434 6.1074 17.8926C9.35656 21.1417 14.6434 21.1417 17.8925 17.8926C21.1417 14.6434 21.1417 9.3566 17.8925 6.10744C14.6434 2.85829 9.35656 2.85829 6.1074 6.10744ZM16.714 16.714C14.1148 19.3133 9.88512 19.3133 7.28591 16.714C4.68671 14.1148 4.68671 9.88516 7.28591 7.28595C9.88512 4.68675 14.1148 4.68675 16.714 7.28595C19.3132 9.88516 19.3132 14.1148 16.714 16.714Z' fill='%2368676C'/%3E%3C/svg%3E%0A");
}
.hidden{
    display: none !important;
}
textarea{
    flex: 1;
    height: 100%; /* TODO: Find corresponding height to adjustTextArea() */
    font-size: 1.2em;
    padding: 8px;
    margin: 0;
    float: left;
    border: 1px solid lightgray;
    resize: none;
    font-family: inherit;
}
/* https://tympanus.net/codrops/2015/09/15/styling-customizing-file-inputs-smart-way/ */
input { /* Hide input element */
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    display: none;
    overflow: hidden;
    position: absolute;
    z-index: -1;
}
div{
    margin-top: 2vh;
    height: /*calc(var(--vh, 1vh) * 20)*/ 125px;
    display: flex;
    flex-direction: row;
}
.round-border{
    border-radius: 5px;
}
.inner-border{
    box-sizing: border-box;
}
.img-scale{
    max-width: 29vw;
    margin-right: 2vw;
    max-height: 100%;
}
@media only screen and (min-width: 640px) {
    .img-scale{
        max-width: 15vw;
    }
}
.img-scale.container{
    height: 8000px; width: 8000px;
    margin-top: 0;
    position: relative;
    display: inline-block;
    float: left;
}
.img-scale.container > img{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
}
label.img-scale {
    float: left;
    display: inline-block;
    position: relative;
    cursor: pointer;
    /* TODO: Better way of filling max-height? */ width: 8000px; height: 8000px;
    border: 1px dashed #C4C4C4;
}
label.img-scale > svg{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
}
</style>