<template>
    <div class="container" @touchstart="this.touchHandler">
        <div ref="controlContainer" class="control-bar">
            <v-dialog />
            <div class="control-container">
                <button 
                :class="this.appState === this.appStates.welcome ? 'active' : ''"
                :disabled="this.disableControls"
                class="wrapper-button home" 
                @click="homeClick"></button>
            </div>
            <div class="control-container">
                <button 
                :class="this.appState === this.appStates.info ? 'active' : ''"
                :disabled="this.disableControls"
                class="wrapper-button info" 
                @click="infoClick"></button>
                <button class="my-control first mapboxgl-ctrl mapboxgl-ctrl-group" @click="$emit('forget-me')">..</button>
            </div>
            <!--:class="{hide: this.appState === this.appStates.welcome}"-->
            <div class="control-container">
                <button 
                ref="navBtn"
                :class="
                (this.appState === this.appStates.map || 
                this.appState === this.appStates.low || 
                this.appState === this.appStates.high) && 
                !(this.disableControls || this.disableNav) ? 'active' : ''"
                :disabled="this.disableControls || this.disableNav"
                class="wrapper-button zoom" 
                @click="findClick"></button>
            </div>
            <!--:class="{hide: this.appState === this.appStates.welcome}"-->
            <div class="control-container">
                <button
                :class="this.appState === this.appStates.create || this.appState === this.appStates.place ? 'active' : ''"
                :disabled="this.disableControls"
                class="wrapper-button map" 
                @click="navClick">
                </button>
            </div>
            <div class="control-container">
                <button 
                :class="this.appState === this.appStates.profile ? 'active' : ''"
                :disabled="this.disableControls"
                class="wrapper-button profile" 
                @click="profileClick"></button>
            </div>
        </div>
        <!--<span class="profile-block">
            {{user.username}}
            <br v-if="treasuresData!=undefined">
            <span v-if="treasuresData!=undefined"> {{this.userStats[0]}}<br>{{this.userStats[1]}} </span>
        </span>-->
    </div>
</template>

<script>

export default {
    name: 'myBar',
    props: ['user','showProfile','appState','appStates'],
    data(){
        return{
            treasuresData: undefined,
            activeClass: '',
            disableControls: true,
            disableNav: false,
            useDebugNav: undefined,
            geolocateFun: undefined
        }
    },
    methods: {
        setStats(treasuresData){
            this.treasuresData = treasuresData;
        },
        infoClick(){
            //e.target.nextSibling.click();
            if(this.appState === this.appStates.info) this.$emit('change-state',this.appStates.map);
            else this.$emit('change-state',this.appStates.info);
        },
        navClick(e){
            let wrappedNav = e.target.nextSibling;
            if(this.appState === this.appStates.create || this.appState === this.appStates.place) this.$emit('change-state',this.appStates.map);
            else {
                let closeDialog = () => {
                    this.$modal.hide('dialog');
                }
                this.$modal.show('dialog',{
                    width: '450px',
                    title: ' ',
                    text: ' :',
                    buttons: [
                        {
                            title: ' ',
                            handler: () => {
                                if (navigator.geolocation) {
                                    navigator.geolocation.getCurrentPosition(()=>{}, (error)=>{
                                        switch(error.code) {
                                            case error.PERMISSION_DENIED: alert("    ."); break;
                                            case error.POSITION_UNAVAILABLE: alert("   ."); break;
                                            case error.TIMEOUT: alert("       ."); break;
                                            case error.UNKNOWN_ERROR: alert("  ."); break;
                                        }
                                    });
                                } else 
                                    alert("    .");

                                wrappedNav.click(); 
                                closeDialog(); 
                            }
                        },
                        {
                            title: '  ',
                            handler: () => { this.$emit('change-state', this.appStates.place); closeDialog(); }
                        },
                        {
                            title: ''
                        }
                    ]
                })
            }
        },
        profileClick(){
            //e.target.nextSibling.click();
            let state;
            if(this.appState === this.appStates.profile) state = this.appStates.map;
            else state = this.appStates.profile;
            this.$emit('change-state',state);
        },
        homeClick(){
            let state;
            if(this.appState === this.appStates.welcome) state = this.appStates.map;
            else state = this.appStates.welcome;
            this.$emit('change-state',state);
        },
        findClick(e){
            let wrappedNav = e.target.nextSibling;
            wrappedNav.click();
        },
        addControls(){
            /*geolocate.control.on('error',(data)=>{
                this.disableNav=true; 
                alert(data.message); 
            });*/
            let controls = [undefined,undefined,undefined,...[].slice.call(arguments)];
            let containers = [...this.$refs.controlContainer.childNodes];
            controls.forEach((control,index)=>{
                let container = containers[index];
                if(control == undefined) return;
                if(container == undefined){
                    console.error('Unable to display all controls, not enough containers'); 
                    return;
                }
                container.appendChild(control);
            });
            this.disableControls = false;
        },
        touchHandler(){ //https://stackoverflow.com/a/37712966
            if(event.touches.length > 1){
                //the event is multi-touch
                //you can then prevent the behavior
                event.preventDefault()
            }
        }
    },
    computed: {
        userStats(){
            console.log(this.treasuresData.createdTreasures())
            return ['Created: '+this.treasuresData.createdTreasures().length,'Found: '+ this.treasuresData.foundTreasures().length];
        },
        controlContainer(){
            return this.$refs.controlContainer;
        }
    },
    mounted(){
        //console.log(this.$refs.controlContainer ? "Emitting control container" : "Emitting undefined");
        this.$emit('mounted');
    }
}
</script>

<style scoped>
.control-bar{
    position: relative;
    display: flex;
    margin-left: auto;
    margin-right: auto;
    margin-top: 2px;
    margin-bottom: 0;
    height: 90%; /* FIXME: Weird scaling */
    width: 100%;
    /*border-left: 1px solid lightgray;
    border-right: 1px solid lightgray;*/
}
.control-container{
    display: inline-block;
    flex: 1;
    position: relative;
}
.control-container.hide{
    display: none;
}
.container{
    overflow: hidden;
    position: relative;
    display: block;
    height: 100%;
    width: 100%;
}
.wrapper-button{
    background-color: #F5F5F5;
    border: none;
    padding: 20px;
    display: inline-block;
    background-repeat: no-repeat;
    background-position: center;
    border-radius: 50%;
    box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.05);
}
.wrapper-button:disabled{
    background-color: lightgray;
}
