import Vue from 'vue'
import App from './App.vue'
import router from './router'
import VModal from 'vue-js-modal'

Vue.config.productionTip = false;
let hostname = window.webpackHotUpdate ?
  'https://192.168.1.31:8443' : //send requests to this address in development mode
  ''; //send requests to this address when compiled (leave blank if backend is deployed on the same ip&port)


Vue.use(VModal, { dialog: true });

Vue.prototype.$updateMsInterval = undefined/* 30000 */;
Vue.prototype.$hostname = hostname;
Vue.prototype.$endpoints = {
  me: hostname + '/api/user/me',
  treasures: hostname + '/api/treasure/all',
  login: hostname + '/login',
  logout: hostname + '/api/user/logout',
  uploadImage: hostname + '/api/images/upload',
  createTreasure: hostname + '/api/treasure/create',
  changeProfileImage: hostname + `/api/user/me/set-profile-image`,
  setDepartment: hostname + `/api/user/me/set-department`,
  appStats: hostname + `/public/app-stats`,
  imageByUrl(imgFilenameURL) { 
    try {
      // Throws TypeError if not an absolute link w/ hostname
      const url = new URL(imgFilenameURL);
      if (url.host && url.hostname) return imgFilenameURL;
    } catch (error) { return `${hostname}/images/${imgFilenameURL}`; }
    return `${hostname}/images/${imgFilenameURL}`;
  },
  userProfilePicture(user) { return this.imageByUrl(user.imageUrl) },
  userTreasures(id) { return hostname + `/api/user/${id}/treasures` },
  findTreasure(id) { return hostname + `/api/treasure/${id}/find`; },
  editTreasure(id) { return hostname + `/api/treasure/${id}/edit`; },
  likeTreasure(id) { return hostname + `/api/treasure/${id}/like`; },
  postComment(id) { return hostname + `/api/treasure/${id}/comment` },
  likeComment(id, comment) { return hostname + `/api/treasure/${id}/comment/${comment}/like`; }
}

Vue.prototype.$enumNameByIndex = function (index, _enum) { return Object.keys(_enum).find(k => _enum[k] === index); }

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')

