<template>
    <div class="block-container" ref="container">
        <img 
        @load="this.onImgLoad"
        v-if="this.blockData.imgUrl!=undefined" 
        :src="$endpoints.imageByUrl(blockData.imgUrl)">
        <textarea 
        v-if="this.showText"
        ref="descriptionArea" 
        v-model="blockData.textBlock" 
        disabled>
        </textarea>
    </div>
</template>

<script>
export default {
    name: 'descriptionBlock',
    props: ['blockData'],
    data(){
        return{
            descriptionImageHeight: undefined
        }
    },
    computed: {
        showText(){
            return this.blockData.textBlock!=undefined && this.blockData.textBlock.length > 0;
        }
    },
    methods: {
        stopPropagation(e){
            e.stopPropagation();
        },
        onImgLoad(e){
            let el = e.target;
            if(this.showText) this.$refs.descriptionArea.style.width = Math.min(el.getBoundingClientRect().width, el.offsetWidth)+'px';

            let lazyParent = this.$refs.container.parentElement.parentElement.parentElement.parentElement;
            let oldHeight = parseInt( lazyParent.style.getPropertyValue('--dvh').replace('px','').replace(' ','') );
            oldHeight = isNaN( oldHeight ) ? Infinity : oldHeight;
            let curHeight = el.offsetHeight;
            this.descriptionImageHeight = Math.min(oldHeight, curHeight);
            lazyParent.style.setProperty('--dvh', ' '+this.descriptionImageHeight+'px');
        }
    }
}
</script>
<style>
.VueCarousel-inner, .VueCarousel-wrapper{
    height: 100% !important;
}
</style>
<style scoped>
.block-container{
    /* Intial popup height - bar */
    height: calc(var(--vh, 1vh) * 80);
    width: 100%;
    overflow: hidden;
    position: relative;
    display: flex;
    flex-direction: column;
    touch-action: none;
}
.block-container > img{
    display: block;
    position: relative;
    max-height: 60vh;
    max-width: 100%;
}
*{
    margin-left: auto;
    margin-right: auto;
    margin-top: 0;
    margin-bottom: 0;
}
textarea{
    border: 0;
    /*border-left: 1px solid lightgray;
    border-right: 1px solid lightgray;*/
    font-family: inherit;
    box-sizing: border-box;
    flex: 1;
    width: 100%;
    display: block;
    padding: 0.5em 0;
    font-size: 15px;
    resize: none;
}
textarea:disabled{
    background-color: white;
    -webkit-appearance: none;
    color: black !important;
}
@media only screen and (min-width: 640px) {
    img{
        max-width: 50%;
    }
    textarea{
        width: 50%;
    }
}
</style>