<template>
    <div class="welcome-container">
        <div class="title-container">
            <img src="../assets/trovehunt-title.png">
        </div>
        <div class="stats-container">
            <div class="stat">
                <div class="stat vertical">
                    <span>
                        {{this.totalTreasures}}
                    </span>
                    <p>
                        {{this.getCorrectTitle(this.totalTreasures,['', '', ''])}}
                    </p>
                </div>
            </div>
            <div class="stat">
                <div class="stat vertical">
                    <span>
                        {{this.totalCountries}}
                    </span>
                    <p>
                        {{this.getCorrectTitle(this.totalCountries,['', '', ''])}}
                    </p>
                </div>
            </div>
            <div class="stat">
                <div class="stat vertical">
                    <span>
                        {{this.totalCities}}
                    </span>
                    <p>
                        {{this.getCorrectTitle(this.totalCities,['', '', ''])}}
                    </p>
                </div> 
            </div>
        </div>
        <div class="map-container" @click="$emit('show-map')">
            <img src="../assets/trovehunt-map.png">
        </div>
    </div>
</template>

<style scoped>
.welcome-container{
    height: calc(var(--vh, 1vh) * 88);
    display: flex;
    flex-direction: column;
}
@media only screen and (min-width: 640px) {
    .title-container > img{
        display: none;
    }
}
.title-container > img{
    min-width: 100%;
    max-width: 100%;
    max-height: 100%;
}
.stats-container{
    height: calc(var(--vh, 1vh) * 10);
    display: flex;
    margin-top: 4em;
}
.stats-container > .stat{
    flex: 1;
    position: relative;
}
.stat.vertical{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
}

.stat > p {
    line-height: 50%;
    font-size: 110%;
    font-weight: 300;
    margin: 0;
    margin-top: 5px;
}
.stat > span{
    color: #00A460;
    font-weight: bolder;
    line-height: 50%;
    font-size: 250%;
}
.map-container{
    flex: 1;
    position: relative;
}
.map-container > img{
    border-style: none;
    outline: none;
    border: none;
    max-width: 100%;
    max-height: 100%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
}
</style>

<script>
export default {
    name: 'welcomePage',
    data(){
        return{
            totalTreasures: 0,
            totalCountries: 0,
            totalCities: 0
        }
    },
    async mounted(){
        let infoRequest = await fetch(this.$endpoints.appStats);
        let infoData = await infoRequest.json();
        this.totalTreasures = infoData.totalTreasures;
        this.totalCountries = infoData.totalCountries;
        this.totalCities = infoData.totalCities;

        /*for (let i = 0; i < 150; i++) {
            console.log( i + ": " + this.getCorrectTitle(i, ['', '', '']), this.getCorrectTitle(i,['', '', '']), this.getCorrectTitle(i,['', '', '']) )
        }*/
    },
    methods: {
        //https://gist.github.com/realmyst/1262561#file-gistfile1-js
        getCorrectTitle(number, titles){
            let cases = [2, 0, 1, 1, 1, 2];  

            /*count, ['', '', ''];*/

            return titles[ 
                (number%100>4 && number%100<20) ? 
                    2
                    : cases[(number%10<5)?number%10:5] ];
        }
    }
}
</script>