<template>
    <div>
        <h4 @click="goBack"></h4>
        <h3> </h3>
        <div class="users-container">
            <div class="user" v-for="(userFinding,index) in popupData.treasure.userFindings" :key="index">
                <img :src="$endpoints.userProfilePicture(userFinding.user)" class="picture" >
                <span v-if="isLikedByUser(userFinding.user.id)" class="like-icon"></span>
                <div class="user-data-outer">
                    <div class="user-data-inner">
                        <span class="name">{{userFinding.user.username}}</span>
                        <span class="department">{{userFinding.user.department ? userFinding.user.department : ""}}</span><!--{{user.department}}-->
                    </div>
                </div>
            </div>
        </div>
    </div>
    
</template>

<script>
export default {
    name: 'usersOverlay',
    props: ["popupData","appState","appStates","uid","likedList"],
    mounted(){
    },
    methods: {
        goBack(){
            this.$emit("change-state", this.appStates.high);
        },
        isLikedByUser(id){
            return !!this.likedList.find(likeId=>id === likeId);
        }
    }
}
</script>

<style scoped>
h4, h3{
    line-height: 75%;
    text-align: left;
    margin-left: 20px;
    font-weight: normal;
}
span{
    display: inline-block;
}
.picture{
    width: 80px;
    height: 80px;
    border-radius: 50px;
    object-fit: cover;
    /* https://stackoverflow.com/a/24770757 */
    vertical-align: top;
}
.user{
    text-align: left;
    margin-top: 10px;
    padding: 5px 10px;
    width: var( 100% - 10px );
    height: 80px;
    position: relative;
}
.users-container{
    margin-left: 20px;
    height: calc( var(--vh, 1vh) * 65);
    overflow-y: scroll;
}
.user-data-outer{
    position: relative;
    display: inline-block;
    height: 80px;
}
.user-data-inner{
    position: absolute;
    top: 50%;
    margin-left: 20px;
    transform: translateY(-50%);
}
.user-data-inner > span{
    width: 200px;
}
.name{
    line-height: 100%;
    color: #00A460;
    display: block;
}
.department{
