<template>
  <div class="screen-overlay" style="overflow: auto;">
    <span class="page-label"></span>
        <div class="user-info">
            <!--
            <div class="third">
                <div class="container left">
                <span class="user balance">{{totalBalance}}</span>
                <span class="label"></span>
                </div>
            </div>
            -->
            <div class="third">
                <div class="container left">
                    <span>: </span>
                    <span class="count created">{{totalCreated}}<span class="label"></span></span>
                    <span class="count found">{{totalFound}}<span class="label"></span></span>
                </div>
            </div>
            <div class="third avatar">
                <img :src="$endpoints.userProfilePicture(user)" @click="$refs.userImageInput.click()">
                <input ref="userImageInput" type="file" style="display: none;" @change="onFileSelected">
            </div>
            <div class="third">
                <div class="container right">
                <span class="username">{{user.username}}</span>
                </div>
            </div>
        </div>
        <div class="treasure-list">
            <div 
            class="treasure" 
            v-for="(elem, index) in getProfileTreasures"
            :key="index"
            :class="[$enumNameByIndex(elem.type, treasureTypes)]"
            @click="showTreasureOnMap(elem)">
                <span class="reward-location">{{elem.formattedLocation}}</span>
                <span 
                alt="Image not found" class="img-ico" 
                :class="{
                    'shovel' : elem.type === treasureTypes.found, 
                    'treasure' : elem.type === treasureTypes.created, 
                    'coin' : elem.type === treasureTypes.user
                }"></span>
                <span class="balance">+{{elem.type === treasureTypes.created ? elem.rewardCreated : (elem.type === treasureTypes.found ? elem.rewardFound : elem.rewardUser) }}</span>
                <span>{{ epochToString(elem.showDate) }} <!--<a class="treasure-detail" :class="{'user':elem.type === treasureTypes.user}" href="https://yandex.ru/"></a>--> </span>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  name: 'Profile',
  props: ['user','treasuresData','treasureTypes'],
  data(){
    return{
      treasures:undefined
    }
  },
  methods: {
      epochToString(epoch){
        return new Date(epoch).toLocaleDateString();
      },
      showTreasureOnMap(treasure){
        this.$emit('show-treasure-on-map',[treasure.longitude, treasure.latitude])
      },
      async onFileSelected(){
        // FIXME: User profile picture is not updated in treasureFinding list and comments
        let file = this.$refs.userImageInput.files[0];
        const data = new FormData();
        data.append('file',file);
        const uploadFetch = await fetch(this.$endpoints.changeProfileImage, {
            credentials: 'include', 
            method: 'post',
            body: data
        });
        if(!uploadFetch.ok){
            console.log(uploadFetch);
            alert('Error occured. Check dev console for details');
        }
        const uploadFetchJson = await uploadFetch.json();
        this.user.imageUrl = uploadFetchJson.imageUrl;
      }
  },
  computed: {
      getProfileTreasures(){
        let td = this.treasuresData;
        let ret = [...td.createdTreasures,...td.foundTreasures];
        let userFindings = [];
        ret.forEach(t=> {
            let type,showDate;
            if(t.author.username === this.user.username){
                t.userFindings.forEach((f,i)=>{
                    let fbuPush = JSON.parse( JSON.stringify(t) );
                    fbuPush.type = this.treasureTypes.user;
                    fbuPush.showDate = f.date;
                    fbuPush.foundIndex = i;
                    //fbuPush.location.rewardUser = 45;
                    userFindings.push(fbuPush);
                });
                showDate = t.date;
                type = this.treasureTypes.created;
            }
            else{
                showDate = t.userFindings.filter(t=>t.user.username === this.user.username)[0].date;
                type = this.treasureTypes.found;
            }
            t.type = type;
            t.showDate = showDate;
        });
        ret.push(...userFindings);
        ret.sort((t1,t2)=>t2.showDate-t1.showDate);
        //ret.forEach(t=>console.log(new Date( t.showDate ).toLocaleString() + (t.created ? 'created' : 'found')));
        return ret;
      },
      totalBalance(){
        let ret = 0;
        this.getProfileTreasures.forEach((t)=>{
            ret += t.type === this.treasureTypes.created ? t.rewardCreated : t.rewardFound;
        });
        return ret;
      },
      totalFound(){
        return this.getProfileTreasures.filter((t) => t.type === this.treasureTypes.found).length;
      },
      totalCreated(){
        return this.getProfileTreasures.filter((t) => t.type === this.treasureTypes.created).length;
      }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
