    display: flex;
    flex-direction: column;
}
.treasure-list{
    flex: 1;
    overflow: auto;
    padding: 0 3vw;
}
.page-label{
    display: block;
    margin-top: 10px;
    margin-bottom: 5px;
    margin-left: 5vw;
    font-size: x-large;
    float: left;
}
.user-info{
    display: flex;
    width: 100%;
    height: 5em;
    text-align: center;
    background: #F5F5F5;
}
.third{
    position: relative;
    flex: 1;
}
.third.avatar{
    flex: none;
    width: 30vw;
}
.third.avatar > img{
    border-radius: 50%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    max-height: 100%;
    max-width: 100%;
    object-fit: cover;
    height: 80px;
    width: 80px;

}
.username{
    display: block;
    font-size: large;
    font-weight: normal;
    text-align: center;
}
.treasure {
    position: relative;
    overflow: auto;
    padding: 15px;
    margin-top: 10px;
    box-sizing: border-box;
    border-radius: 5px;
}
.treasure span{
    color: white;
    width: 100%;
    font-size: 15px;
    line-height: 1em;
    text-align: left;
    display: block;
}
.treasure.user{
    background: #E5E5E5;
}
.treasure.user > span{
    color: #68676C;
}
.treasure.found{
    background: #475DEB;
}
.treasure.created {
    background-color: #00A460;
}
.beavers{
    position: relative;
    height: 100%;
    width: 100%;
}
.container{
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    position: absolute;
}
.container > .user.balance{
    margin-top: 10px;
}
.container.left span{
    line-height: 100%;
    white-space: nowrap;
    overflow: hidden;
}
.balance{
    font-size: 200% !important;
    margin-bottom: 5px;
    line-height: 80%;
}
.container > .balance{
    display: block;
}
.container > span{
    width: 100%;
    display: inline-block;
    text-align: left;
}
.container > .count > .label{
    color: black;
    margin-left: 5px;
}
.container > .count.created{
    color: #00A460;
}
.container > .count.found{
    color: #475DEB;
}
.img-ico{
    background-repeat: no-repeat;
    background-position-x: right;
    position: absolute;
    display: block;
    width: 28px !important;
    height: 28px;
    right: 20px;
    top: 20px;
}
