    background-image: url("data:image/svg+xml,%3Csvg width='28' height='20' viewBox='0 0 28 20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M23.5632 0H23.3063H20.0326H7.9674H4.69371H4.43676C1.99058 0 0 1.85433 0 4.13307V7.06299V19.4842C0 19.7693 0.248506 20.0008 0.554489 20.0008H4.69456H7.9674H20.0326H23.3054H27.4455C27.7515 20.0008 28 19.7693 28 19.4842V7.06299V4.13307C28 1.85433 26.0094 0 23.5632 0ZM1.10813 7.57874H4.13923V8.66142C2.99137 8.74961 2.08525 9.64724 2.08525 10.7394C2.08525 11.8315 2.99052 12.7291 4.13923 12.8173V18.9669H1.10813V7.57874ZM10.0526 11.7929H4.32434C3.70054 11.7929 3.19338 11.3205 3.19338 10.7394C3.19338 10.1583 3.70054 9.68583 4.32434 9.68583H10.0526C10.3578 9.68583 10.6071 9.45433 10.6071 9.16929V7.57874H17.3929V9.17008C17.3929 9.45433 17.6414 9.68661 17.9474 9.68661H23.6757C24.2995 9.68661 24.8066 10.1591 24.8066 10.7402C24.8066 11.3213 24.2995 11.7937 23.6757 11.7937H17.9474C17.6414 11.7937 17.3929 12.0252 17.3929 12.3102V13.9016H10.6071V12.3102C10.6071 12.0244 10.3586 11.7929 10.0526 11.7929ZM9.499 8.65433H8.52189V7.57874H9.499V8.65433ZM19.479 8.65433H18.501V7.57874H19.479V8.65433ZM7.41291 7.57874V8.65354H5.2482V7.57874H7.41291ZM20.5871 8.65433V7.57874H22.7518V8.65354H20.5871V8.65433ZM17.9474 6.54646H10.0535H1.10813V4.64882H26.8919V6.54646H17.9474ZM22.7518 3.61732H20.5871V1.03228H22.7518V3.61732ZM20.5871 12.8252H22.7518V18.9669H20.5871V12.8252ZM23.8608 3.61732V1.04567C25.3729 1.17087 26.5969 2.24173 26.8454 3.61732H23.8608ZM19.479 1.03228V3.61654H8.52104V1.03228H19.479ZM7.41291 3.61732H5.2482V1.03228H7.41291V3.61732ZM4.14007 1.04567V3.61732H1.15547C1.40397 2.24173 2.62791 1.17087 4.14007 1.04567ZM5.2482 12.8252H7.41291V18.9669H5.2482V12.8252ZM8.52104 12.8252H9.49816V14.4165C9.49816 14.7016 9.74666 14.9331 10.0526 14.9331H17.9465C18.2525 14.9331 18.501 14.7016 18.501 14.4165V12.8252H19.479V18.9669H8.52104V12.8252ZM23.8608 12.8181C25.0086 12.7299 25.9147 11.8323 25.9147 10.7402C25.9147 9.64803 25.0095 8.75039 23.8608 8.66