<template>
    <div ref="digoutOverlay" class="screen-overlay digout-overlay">
            <!--<div class="close-button-container">
                <button @click="$emit('close-digout-overlay')">&lt;-</button>
            </div>-->
            <div style="flex: 1;" v-if="this.showReward">
                <div class="reward-info-container">
                    <h3>!</h3>
                    <h2>+{{findFetchResult.rewardFound}}</h2>
                    <p>     </p>
                </div>
                <div class="confirm-button-container">
                    <button class="continue-button" @click="$emit('success-digout')"></button>
                </div>
            </div>
            <div v-if="!this.showReward">
                <h1> </h1>
                <h4>  ,    </h4>
                <div v-bind:class="{border: this.previewImage == undefined}" class="selfie-preview-container" @click="selectImage(false)">
                    <img class="selfie-image" v-if="this.previewImage != undefined" @click="selectImage(true)" v-bind:src='previewImageUrl'>
                    <!--<div class="selfie-image-container">
                        <button class="delete-selfie-image" style="margin: 0; padding: 0; border: 0;">X</button>
                        <div class="selfie-image" v-if="this.previewImage != undefined" v-bind:style="[previewImageUrl ? {'background-image': `url(${previewImageUrl})`} : '']">

                        </div>
                    </div>-->
                    <svg v-if="this.previewImage == undefined" width="95" height="85" viewBox="0 0 38 34" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path d="M34.6957 8.09524H29.4583L26.4348 4.85714H16.5217V8.09524H24.9809L28.0043 11.3333H34.6957V30.7619H8.26087V16.1905H4.95652V30.7619C4.95652 32.5429 6.44348 34 8.26087 34H34.6957C36.513 34 38 32.5429 38 30.7619V11.3333C38 9.55238 36.513 8.09524 34.6957 8.09524ZM13.2174 21.0476C13.2174 25.5162 16.9183 29.1429 21.4783 29.1429C26.0383 29.1429 29.7391 25.5162 29.7391 21.0476C29.7391 16.579 26.0383 12.9524 21.4783 12.9524C16.9183 12.9524 13.2174 16.579 13.2174 21.0476ZM21.4783 16.1905C24.2043 16.1905 26.4348 18.3762 26.4348 21.0476C26.4348 23.719 24.2043 25.9048 21.4783 25.9048C18.7522 25.9048 16.5217 23.719 16.5217 21.0476C16.5217 18.3762 18.7522 16.1905 21.4783 16.1905ZM8.26087 8.09524H13.2174V4.85714H8.26087V0H4.95652V4.85714H0V8.09524H4.95652V12.9524H8.26087V8.09524Z" fill="#C4C4C4"/>
                    </svg>
                </div>
                <div class="confirm-button-container">
                    <a href="mailto:email@example-company.ru">  /</a>
                    <button class="continue-button" @click="confirmFinding" :disabled="this.disableContinueButton">  </button>
                </div>
            </div>        
    </div>
</template>

<script>
import rescaleImgCanvas from '@/RescaleImgCanvas.js'

export default {
    name: 'digoutOverlay',
    props: ['digoutOverlayData'],
    data(){
        return{
            previewImage: undefined,
            findFetchResult: undefined,
            previewImageUrl: undefined,
            showReward: false,
            disableContinueButton: true,
        }
    },
    methods: {
        //TODO: https://stackoverflow.com/a/38810874 ios support
        selectImage(override){
            if(this.previewImage != undefined && !override) return;
            let input = document.createElement('input');
            input.type = 'file';
            input.accept = 'image/*';
            input.click();
            input.style.display = 'none';
            this.$refs.digoutOverlay.appendChild(input);
            input.addEventListener('change',e => {
                let file = e.target.files[0];
                let fr = new FileReader();
                fr.onload = () => {
                    this.previewImageUrl = fr.result;
                }
                fr.readAsDataURL(file);
                this.previewImage = file;
                this.disableContinueButton = false;
            });
        },
        async confirmFinding(e){

            e.target.disabled = true;
            let data = new FormData();
            let img = new Image;
            img.src = URL.createObjectURL(this.previewImage);
            
            img.onload = () => {
                let rescaledCanvas = rescaleImgCanvas(img, Math.min(img.width, 450), Math.min(img.height, 450));
                rescaledCanvas.toBlob( async (blob) => {
                    let file = this.previewImage;
                    file = new File([blob],file.name, {type: file.type});
                    data.append('file', file);
                    let uploadFetch = await fetch(this.$endpoints.uploadImage, {
                        credentials: 'include',
                        method: 'post',
                        body: data
                    });
                    if(!uploadFetch.ok){
                        console.log(uploadFetch);
                        alert('Error occured. Check console for details');
                    }
                    let uploadedResponse = await uploadFetch.json();
                    let imgUrl = uploadedResponse.url;

                    let findFetch = await fetch(this.$endpoints.findTreasure(this.digoutOverlayData.treasure.id),{
                        method: 'put',
                        credentials: 'include',
                        body: JSON.stringify({imageUrl:imgUrl}),
                        headers:{ 'Content-Type': 'application/json' }
                    });

                    if(!findFetch.ok){
                        console.log(findFetch);
                        alert('Error occured. Check console for details');
                    }
                    this.findFetchResult = await findFetch.json();
                    this.$emit('update-markers');
                    this.showReward = true;
                })
            }
        }
    }
}
</script>

<style scoped>
h1,h4{
    line-height: 115%;
    font-weight: normal;
    text-align: left;
    margin: 5px 0 5px 0;
}

h4{
    color: #68676C;
}
h2{
    font-size: 250%;
    color:#00A460;
}
.digout-overlay{
    display: flex;
    flex-direction: column;
    margin-left: auto;
    margin-right: auto;
    width: 95% !important;
    /*padding-left: 1% 0 0 1%;*/
}
@media only screen and (device-height : 800px) { 
        .digout-overlay{
            height: 73vh !important;
        }
    }
/*.close-button-container{
    display: block;
    height: 3em;
}
.close-button-container button{
    border: 1px solid lightgray;
    height: 3em;
    width: 3em;
    float: left;
    border: 0;
}*/
.selfie-preview-container{
    position: relative;
    width: 100%;
    height: 40vh;
}
.selfie-preview-container.border{
    border: 1px dashed #C4C4C4;
    border-radius: 5px;
}
.selfie-preview-container > svg{
    margin: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
.selfie-image-container{
    position: relative;
    width: 100%;
    height: 100%;
}
.selfie-image-background{
    height: 100%;
    width: auto;
    background-origin: content-box;
    background-position-x: center;
    background-size: contain;
    background-repeat: no-repeat;
}
.selfie-image{
    max-height: 100%;
    max-width: 100%;
    width: auto;
}
.delete-selfie-image{
    position: absolute;
    top: 0;
    right: 0;
}

</style>