<template>
  <div class="container">
    <div class="main-view">
      <MyMap 
      class="my-map"
      v-bind:user="user"
      v-if="user!=undefined" />
      <Login class="col-login" v-if="user==undefined" 
      v-on:network-error="networkError"
      v-on:auth-success="authSuccess" 
      v-on:auth-fail="authFail"/>
      <modal 
      name="dep-request"
      :clickToClose=false
      :width="320"
      :height="200">
          <div class="dep-request-container">
              <h3> !</h3>
              <h5>,   :</h5>
              <form @submit.prevent="depClick">
                <input ref="depInput" @input="depInput" />
                <br>
                <input :disabled="this.disableDepBtn" class="continue-button" value="" type="submit"/>
              </form>
          </div>
      </modal>
      <!--<Profile v-bind:user="user" v-if="user!=undefined" />-->
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import Login from '@/components/Login.vue'
import MyMap from '@/components/MyMap.vue'

export default {
  name: 'mainView',
  components: {
    Login,
    MyMap
  },
  data(){
    return{
      user: undefined,
      disableDepBtn: true
    }
  },
  methods:{
    authSuccess(user){
      this.user=user;
      if(user.department == undefined){
        this.$modal.show('dep-request',{
          width: '450px'
        })
      }
    },
    authFail(){
      alert('Bad credentials');
    },
    networkError(error){
      console.error(error);
      console.warn('Failed connecting to the server, check logs for more details.');
    //   alert('Failed connecting to the server, please try again later.');
    },
    depInput(e){
      let trim = e.target.value.trim();
      this.disableDepBtn = trim.length === 0 || trim.length > 25;
    },
    depClick(){      
      this.$modal.hide('dep-request');
      let dep = this.$refs.depInput.value;
      fetch(this.$endpoints.setDepartment, {
        credentials: 'include',
        method: 'put',
        body: dep
      }).then((r)=>r.json().then((json)=>{
        this.user = json;
      }));
    }
  }
}
</script>

<style scoped>
.dep-request-container{
    width: 250px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
}
.dep-request-container h3,
.dep-request-container h5{
    margin-top: 5px;
    margin-bottom: 5px;
}
.dep-request-container input {
    border: 1px solid #ccc;
    padding: 5px 0;
    padding-left: 3px;
    width: 100%;
}
  /*@media only screen and (orientation:landscape){
    *{
      display: none;
    }
    .main-view,.container{
      display: block !important;
    }
    .main-view::after{
      content: '   .';
    }
  }*/
  .col-login{
    display: block;
    padding-top: 10%;
    width: 90%;
    margin-left: 5%;
    position: relative;
    margin-left: auto;
    margin-right: auto;
  }
  @media only screen and (min-width: 640px) {
    /*phone only*/
    .col-login{
      width: 20%;
    }
  }
  .main-view{
    position: relative;
    width: 100%;
    height: 100%;
  }
  .my-map{
    height: 100%;
  }
  .container{
    height: 100%;
  }
</style>