<template>
    <form name="login" @submit.prevent="submitLogin">
        <h2 class="app-title">Trovehunt</h2>
        <input type="text" placeholder="" name="username" v-model="username">
        <input type="password" placeholder="" name="password" v-model="password">
        <a class="forgot-password" href="mailto:email@example-company.ru"> ?</a>
        <input class="continue-button" :disabled="this.disableLogin" style="font-size: 105%" type="submit" name="submit" value="">
    </form>
</template>

<script>

export default {
    name: 'Login',
    mounted(){
        this.tryCookieGeo(true);
    },
    data(){
        return{
            username: '',
            password: '',
            disableLogin: false
        }
    },
    methods: {
        submitLogin(){
            let username = this.username;
            let password = this.password;
            if(username==undefined||username.length<1||password==undefined||password.length<1){ alert('Please, fill in your credentials'); throw new Error('Empty credentials'); }
            let data = new FormData();
            data.append('username',username);
            data.append('password',password);
            this.sendLoginForm(data);
        },
        sendLoginForm(form){
            this.disableLogin = true;
            fetch(this.$endpoints.login,{
                method: 'post',
                body: form,
                credentials: 'include'
            })
            .then(()=>this.tryCookieGeo(false));

        },
        tryCookieGeo(silent){
            let catchedError = false;
            fetch(this.$endpoints.me,{
                method: 'get',
                credentials:'include'
            })
            .catch((error)=>{ catchedError = true; this.$emit('network-error',error)})
            .then((response)=>{
                if(!response.redirected && !catchedError && document.forms['login'] && document.forms['login']['username']){
                    document.forms['login']['username'].disabled=true;
                    document.forms['login']['password'].disabled=true;
                    document.forms['login']['submit'].disabled=true;

                    response.json().then((json)=>{ this.$emit('auth-success',json); });
                }
                else if(!silent) this.$emit('auth-fail');
            })
            .finally(()=>this.disableLogin = false);
        }
    }
}
</script>

 
