<script>
import './assets/styles/fonts.css'

export default {
  name: 'app',
  mounted(){
    this.setVhVar();
    window.addEventListener('resize', this.setVhVar );
  },
  methods: {
    setVhVar(){
      let vh = window.innerHeight * 0.01;
      document.documentElement.style.setProperty('--vh', `${vh}px`);
    }
  }
}
</script>

